"use client";

import React, { useState, useRef, useEffect } from "react";
import {
  Home,
  Hash,
  Heart,
  Clapperboard,
  Bell,
  User,
  Upload,
  MessageSquare,
  Moon,
  ChevronDown,
  ChevronUp,
  Menu,
  X,
} from "lucide-react";

// Mock useAuth context since the original import is from an external path
// In a real environment, you would use: import { useAuth } from "@/context/AuthContext";
const useAuth = () => ({
  user: { name: "Guest User", _id: "12345" },
  logout: () => alert("Logged out!"),
});

const COLORS = {
  BACKGROUND_PRIMARY: "bg-[#131313]",
  BACKGROUND_SECONDARY: "bg-gray-800",
  BORDER_COLOR: "border-gray-700", // Adjusted slightly for better contrast
  TEXT_PRIMARY: "text-white",
  TEXT_SECONDARY: "text-gray-300",
  TEXT_HEADER: "text-gray-500",
  ACTIVE_ACCENT: "border-white",
  ACTIVE_BACKGROUND: "bg-gray-800",
};

const menuSections = [
  {
    title: "MAIN MENU",
    links: [
      { name: "Home Page", icon: Home, isActive: true },
      { name: "Trending Topics", icon: Hash },
      { name: "Popular Creator", icon: Heart },
      { name: "Reels", icon: Clapperboard },
      { name: "Notification", icon: Bell },
    ],
    defaultOpen: true,
  },
  {
    title: "MY MENU",
    links: [
      { name: "Profile", icon: User },
      { name: "Shot Upload", icon: Upload },
      { name: "Message", icon: MessageSquare },
    ],
    defaultOpen: true,
  },
];

const MenuItem = ({ icon: Icon, name, isActive = false }) => (
  <div
    className={`flex items-center p-3 my-0.5 rounded-lg transition-colors cursor-pointer text-sm
      ${
        isActive
          ? `${COLORS.ACTIVE_BACKGROUND} ${COLORS.TEXT_PRIMARY} font-semibold border-r-4 ${COLORS.ACTIVE_ACCENT}`
          : `${COLORS.TEXT_SECONDARY} opacity-80 hover:${COLORS.BACKGROUND_SECONDARY}/50 hover:${COLORS.TEXT_PRIMARY}`
      }`}
  >
    <Icon className="w-5 h-5 mr-3" />
    <span>{name}</span>
  </div>
);

const MenuSection = ({ section }) => {
  const [isOpen, setIsOpen] = useState(section.defaultOpen);

  return (
    <div className="mb-6">
      <div
        className={`flex items-center justify-between text-xs font-bold tracking-widest ${COLORS.TEXT_HEADER} uppercase cursor-pointer mb-2`}
        onClick={() => setIsOpen(!isOpen)}
      >
        {section.title}
        {isOpen ? (
          <ChevronUp className="w-3 h-3 text-gray-400" />
        ) : (
          <ChevronDown className="w-3 h-3 text-gray-400" />
        )}
      </div>

      {isOpen && (
        <nav>
          {section.links.map((link, index) => (
            <MenuItem key={index} {...link} />
          ))}
        </nav>
      )}
    </div>
  );
};

const Sidebar = () => {
  const [isDarkMode, setIsDarkMode] = useState(true);
  const [open, setOpen] = useState(false);
  const sidebarRef = useRef(null); // Ref for the sidebar element

  const { user, logout } = useAuth();

  const avatarSrc =
    user?.avatarUrl ||
    `https://placehold.co/40x40/131313/ffffff?text=${
      user?.name?.[0] || 'G'
    }`;


  // --- New Robust Outside Click Logic ---
  useEffect(() => {
    if (!open) return; // Only run the listener when the sidebar is open

    const handleClickOutside = (event) => {
      // If the sidebar is open and the click target is outside the sidebar element, close it.
      if (sidebarRef.current && !sidebarRef.current.contains(event.target)) {
        setOpen(false);
      }
    };

    // Use mousedown instead of click for more immediate detection and better reliability
    document.addEventListener("mousedown", handleClickOutside);

    // Cleanup: remove the event listener when the component unmounts or 'open' changes to false
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [open]);
  // -------------------------------------


  return (
    <>
      {/* Mobile Hamburger Button */}
      <button
        className="fixed top-4 left-4 bg-[#1c1c1c] p-2 rounded-xl md:hidden z-50 shadow-lg"
        onClick={() => setOpen(true)}
      >
        <Menu className="text-white w-6 h-6" />
      </button>

      {/* BACKDROP - We keep the visual backdrop when open */}
      {open && (
        <div
          className="fixed inset-0 bg-black bg-opacity-60 backdrop-blur-sm md:hidden z-40"
          // We rely on the useEffect document listener for closing, but adding the click handler here 
          // provides a direct, fast response for the area covered by the backdrop.
          onClick={() => setOpen(false)} 
        ></div>
      )}

      {/* SIDEBAR */}
      <aside
        ref={sidebarRef} // Attach ref here
        className={`
          fixed top-0 left-0 h-screen w-64 p-4 z-50 shadow-2xl rounded-r-xl 
          ${COLORS.BACKGROUND_PRIMARY} ${COLORS.TEXT_PRIMARY} border ${
            COLORS.BORDER_COLOR
          }
          transform transition-transform duration-300

          ${
            open
              ? "translate-x-0 pointer-events-auto"
              : "-translate-x-full pointer-events-none"
          }

          md:pointer-events-auto md:translate-x-0 md:static
        `}
        style={{height: '100vh'}} // Ensure it takes full viewport height
      >
        {/* Close Button Mobile */}
        <div className="md:hidden flex justify-end mb-4">
          <button onClick={() => setOpen(false)}>
            <X className="text-white w-6 h-6 hover:text-red-400" />
          </button>
        </div>

        {/* Logo */}
        <div className="flex items-center mb-8 pt-1">
          <h1 className="text-3xl pl-1 font-extrabold tracking-tight italic text-white">
            DZINR-APP
          </h1>
        </div>

        {/* Menu Sections */}
        <div className="flex-grow overflow-y-auto custom-scroll pr-1">
          {menuSections.map((section, index) => (
            <MenuSection key={index} section={section} />
          ))}
        </div>

        {/* Profile / Login */}
        <div className="my-4 border-t border-gray-700 pt-4">
          {user ? (
            <div className="flex items-center justify-between px-3 py-2 rounded-lg hover:bg-gray-700 cursor-pointer transition-all">
              <div className="flex items-center space-x-3">
                <img
                  src={avatarSrc}
                  alt="Profile"
                  className="w-10 h-10 rounded-full border-2 border-green-500"
                />
                <span className="font-medium capitalize">{user.name}</span>
              </div>
              <button
                onClick={logout}
                className="text-xs bg-red-600 hover:bg-red-700 text-white py-1 px-3 rounded-xl shadow-md"
              >
                Logout
              </button>
            </div>
          ) : (
            <a
              href="/auth/login"
              className="w-full bg-indigo-600 hover:bg-indigo-700 text-white font-semibold py-2 px-8 rounded-xl shadow-lg transition-all duration-200 flex items-center justify-center"
            >
              Login
            </a>
          )}
        </div>

        {/* Dark Mode Toggle */}
        <div
          className={`p-3 ${COLORS.BACKGROUND_SECONDARY} rounded-xl border ${COLORS.BORDER_COLOR} flex items-center justify-between shadow-inner`}
        >
          <div className="flex items-center text-sm">
            <Moon className="w-5 h-5 mr-3 text-yellow-300" />
            <span className="text-white font-medium">Dark Mode</span>
          </div>

          <label className="relative inline-flex items-center cursor-pointer">
            <input
              type="checkbox"
              checked={isDarkMode}
              onChange={() => setIsDarkMode(!isDarkMode)}
              className="sr-only peer"
            />
            <div className="w-12 h-6 bg-gray-600 rounded-full peer-checked:bg-indigo-500 transition-colors duration-300"></div>
            <div
              className={`absolute top-0.5 left-[3px] w-5 h-5 rounded-full transition-transform duration-300 shadow-lg ${
                isDarkMode
                  ? "translate-x-[20px] bg-black border border-white/50"
                  : "translate-x-0 bg-white"
              }`}
            ></div>
          </label>
        </div>
      </aside>
    </>
  );
};

export default Sidebar;