"use client";

import React, { useState, useEffect } from "react";

// --- MOCK IMPLEMENTATIONS FOR RUNNABILITY ---
const useAuth = () => ({ user: { name: "Guest" } });

const PostCreator = () => (
  <div className="bg-[#1c1c1c] p-4 rounded-xl shadow-lg border border-gray-700 text-gray-300">
    <div className="flex items-center space-x-3 mb-3">
      <img
        src="https://placehold.co/40x40/131313/ffffff?text=U"
        alt="User"
        className="w-10 h-10 rounded-full border border-gray-600"
      />
      <input
        type="text"
        placeholder="What's on your mind?"
        className="flex-1 bg-gray-800 border border-gray-700 p-2.5 rounded-full focus:outline-none focus:ring-2 focus:ring-blue-500 text-sm"
      />
    </div>
    <div className="flex justify-between text-sm text-gray-400">
      <button className="flex items-center space-x-1 hover:text-white transition">
        <svg xmlns="http://www.w3.org/2000/svg" className="w-5 h-5" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><rect x="3" y="3" width="18" height="18" rx="2" ry="2"/><circle cx="8.5" cy="8.5" r="1.5"/><polyline points="21 15 16 10 5 21"/></svg>
        <span>Photo/Video</span>
      </button>
      <button className="bg-blue-600 hover:bg-blue-700 text-white font-semibold py-1.5 px-6 rounded-full transition duration-200">
        Post
      </button>
    </div>
  </div>
);

const PostCard = ({ postId }: { postId: string }) => (
  <div className="bg-[#1c1c1c] p-5 rounded-xl shadow-xl border border-gray-700">
    <div className="flex items-center mb-4">
      <img
        src={`https://picsum.photos/40/40?random=${postId}`}
        alt="Author"
        className="w-10 h-10 rounded-full mr-3"
      />
      <div>
        <p className="font-semibold text-white">Mock Author {postId}</p>
        <p className="text-xs text-gray-500">2 hours ago</p>
      </div>
    </div>
    <p className="text-gray-300 mb-4">
      This is a mock post content to demonstrate how the feed will look on different screen sizes. The layout is now fully responsive, ensuring a great experience on mobile devices and adjusting correctly when the sidebars appear on desktop.
    </p>
    <div className="flex justify-around border-t border-gray-700 pt-3 text-gray-400 text-sm">
      <button className="flex items-center space-x-1 hover:text-red-500 transition">
        <svg xmlns="http://www.w3.org/2000/svg" className="w-5 h-5" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M19 14c1.49-1.46 3-3.21 3-5.5A5.5 5.5 0 0 0 16.5 3c-1.76 0-3 .5-4.5 2-1.5-1.5-2.74-2-4.5-2A5.5 5.5 0 0 0 2 8.5c0 2.3 1.5 4.05 3 5.5l7 7Z"/></svg>
        <span>24 Likes</span>
      </button>
      <button className="flex items-center space-x-1 hover:text-blue-400 transition">
        <svg xmlns="http://www.w3.org/2000/svg" className="w-5 h-5" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M21 11.5a8.38 8.38 0 0 1-.9 3.8 15.6 15.6 0 0 1-5.3 4.7c-.5.3-1.2.6-2 .7a.8.8 0 0 1-.7-.1c-.4-.4-1.3-.7-2.3-.9-1.2-.2-2.3-.3-3.6-.3-1.6 0-3.3.3-5 .7l-1 .3V12c0-1.2.3-2.3.8-3.4"/></svg>
        <span>5 Comments</span>
      </button>
      <button className="flex items-center space-x-1 hover:text-green-400 transition">
        <svg xmlns="http://www.w3.org/2000/svg" className="w-5 h-5" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M10 13a5 5 0 0 0 7.54.54l3-3a5 5 0 0 0-7.07-7.07l-1.72 1.71"/><path d="M14 11a5 5 0 0 0-7.54-.54l-3 3a5 5 0 0 0 7.07 7.07l1.71-1.71"/></svg>
        <span>Share</span>
      </button>
    </div>
  </div>
);

// Mock data structure
interface PostFromBackend {
  _id: string;
  author: { name: string; avatarUrl: string };
  feeling?: string;
  withUser?: string;
  createdAt: string;
  text: string;
  media?: { _id: string; url: string; type: "image" | "video"; alt?: string }[];
  likes?: { _id: string; name: string }[];
  comments?: any[]; 
  sharedCount?: number;
}

// --- MAIN COMPONENT ---

const MiddleContent: React.FC = () => {
  const { user } = useAuth();
  const [posts, setPosts] = useState<PostFromBackend[]>([]);
  const [loading, setLoading] = useState(true);

  // Mock fetch to simulate loading data
  useEffect(() => {
    const fetchPosts = async () => {
      // Simulate API call delay
      await new Promise(resolve => setTimeout(resolve, 1000)); 

      const mockData: PostFromBackend[] = [
        { _id: "p1", author: { name: "A", avatarUrl: "" }, createdAt: "2023-01-01", text: "First post content." },
        { _id: "p2", author: { name: "B", avatarUrl: "" }, createdAt: "2023-01-02", text: "Second post content." },
        { _id: "p3", author: { name: "C", avatarUrl: "" }, createdAt: "2023-01-03", text: "Third post content." },
      ];

      try {
        setPosts(mockData);
      } catch (err) {
        console.error("Error fetching posts:", err);
      } finally {
        setLoading(false);
      }
    };

    fetchPosts();
  }, []);

  return (
    <main 
      className="
        flex-1 
        h-screen 
        p-4 
        space-y-4 
        overflow-y-auto 
        scrollbar-custom 
        w-full 
        md:ml-64 
        md:mr-72
        bg-[#0a0a0a]
      "
    >
      <PostCreator />

      {loading
        ? Array.from({ length: 3 }).map((_, idx) => (
            <PostCard key={`loading-${idx}`} postId="" />
          ))
        : posts.map((post) => (
            // Using post._id as a mock postId for the PostCard component
            <PostCard key={post._id} postId={post._id} />
          ))}
    </main>
  );
};

export default MiddleContent;